"use client"

import { useState, useEffect, useCallback } from "react"
import { Contract, formatEther, JsonRpcProvider } from "ethers"
import { getProvider, getRegistryContract, getMarketplaceContract } from "@/lib/web3"
import { CONTRACTS, REGISTRY_ABI, MARKETPLACE_ABI, ModelInfo, ModelMetrics } from "@/lib/contracts"
import toast from "react-hot-toast"

interface LeaderboardModel {
  id: number
  name: string
  owner: string
  strategy: string
  accuracy: number
  totalInferences: number
  revenue: number
  subscribers: number
  icon: string
  inferencePrice: bigint
  latestSignal?: {
    action: "BUY" | "SELL" | "HOLD"
    asset: string
  }
}

const strategyIcons: Record<number, string> = {
  0: "ðŸ¤–",
  1: "ðŸ§ ",
  2: "âš¡",
  3: "ðŸ”®",
  4: "ðŸ“ˆ",
  5: "ðŸŽ¯",
  6: "ðŸ’¡",
  7: "ðŸš€",
  8: "ðŸ”¥",
  9: "ðŸ’Ž",
}

const strategies = ["Momentum", "Mean Reversion", "ML-Based", "Breakout", "Arbitrage"]

/**
 * Hook to fetch and manage real-time model data from contracts
 */
export function useModels(refreshInterval: number = 10000) {
  const [models, setModels] = useState<LeaderboardModel[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  const fetchModels = useCallback(async () => {
    try {
      const provider = getProvider()
      const registry = getRegistryContract(provider)
      const marketplace = getMarketplaceContract(provider)

      // Get total number of models
      let totalModels = 0
      try {
        const nextModelId = await registry.nextModelId()
        totalModels = Number(nextModelId) - 1 // nextModelId is 1-indexed, so subtract 1
      } catch (err) {
        console.error("Error getting model count:", err)
        // Try to fetch models one by one if nextModelId fails
        for (let i = 1; i <= 100; i++) {
          try {
            await registry.getModelInfo(i)
            totalModels = i
          } catch {
            break
          }
        }
      }

      if (totalModels === 0) {
        setModels([])
        setLoading(false)
        return
      }

      // Get top models (or all if less than 10)
      const count = Math.min(totalModels, 50) // Fetch up to 50 models
      const [modelIds, accuracies] = await registry.getTopModels(count)

      // If no top models, fetch all models
      let allModelIds: bigint[] = []
      if (modelIds.length === 0) {
        allModelIds = Array.from({ length: totalModels }, (_, i) => BigInt(i + 1))
      } else {
        allModelIds = modelIds.map((id: bigint) => BigInt(Number(id)))
      }

      // Fetch detailed info for each model
      const modelPromises = allModelIds.map(async (modelId) => {
        try {
          const [modelInfo, modelMetrics, modelStats] = await Promise.all([
            registry.getModelInfo(modelId),
            registry.getModelMetrics(modelId),
            marketplace.getModelStats(modelId).catch(() => ({ subscribers: 0, signals: 0, monthlyPrice: 0n, signalPrice: 0n })),
          ])

      // Get latest signal
      let latestSignal: { action: "BUY" | "SELL" | "HOLD"; asset: string } | undefined
      try {
        const signalCount = await marketplace.getSignalCount(modelId)
        if (Number(signalCount) > 0) {
          const signals = await marketplace.getLatestSignals(modelId, 1)
          if (signals.length > 0 && signals[0]) {
            latestSignal = {
              action: signals[0].action as "BUY" | "SELL" | "HOLD",
              asset: signals[0].asset,
            }
          }
        }
      } catch (err) {
        // No signals yet or marketplace not initialized
        console.debug(`No signals for model ${modelId}`)
      }

          // Extract model name from URI (simplified - in production, fetch from IPFS)
          const modelName = modelInfo.modelURI.includes("Qm") 
            ? `Model #${Number(modelId)}` 
            : modelInfo.modelURI.split("/").pop() || `Model #${Number(modelId)}`

          return {
            id: Number(modelId),
            name: modelName,
            owner: modelInfo.owner,
            strategy: strategies[Number(modelId) % strategies.length],
            accuracy: Number(modelMetrics.accuracy) / 100, // Convert from basis points (0-10000) to percentage
            totalInferences: Number(modelInfo.totalInferences),
            revenue: Number(formatEther(modelMetrics.totalRevenue)),
            subscribers: Number(modelStats.subscribers),
            icon: strategyIcons[Number(modelId) % Object.keys(strategyIcons).length],
            inferencePrice: modelInfo.inferencePrice,
            latestSignal,
          } as LeaderboardModel
        } catch (err) {
          console.error(`Error fetching model ${modelId}:`, err)
          return null
        }
      })

      const fetchedModels = (await Promise.all(modelPromises)).filter(
        (model): model is LeaderboardModel => model !== null
      )

      // Sort by accuracy (descending)
      fetchedModels.sort((a, b) => b.accuracy - a.accuracy)

      setModels(fetchedModels)
      setError(null)
    } catch (err) {
      console.error("Error fetching models:", err)
      setError(err instanceof Error ? err.message : "Failed to fetch models")
      toast.error("Failed to fetch models from contract")
    } finally {
      setLoading(false)
    }
  }, [])

  // Initial fetch
  useEffect(() => {
    fetchModels()
  }, [fetchModels])

  // Set up polling for real-time updates
  useEffect(() => {
    const interval = setInterval(() => {
      fetchModels()
    }, refreshInterval)

    return () => clearInterval(interval)
  }, [fetchModels, refreshInterval])

  // Set up event listeners for real-time updates
  useEffect(() => {
    try {
      const provider = getProvider()
      const registry = getRegistryContract(provider)

      // Listen for new model registrations
      const modelRegisteredFilter = registry.filters.ModelRegistered()
      const performanceUpdatedFilter = registry.filters.PerformanceUpdated()

      const handleModelRegistered = () => {
        console.log("New model registered, refreshing...")
        fetchModels()
      }

      const handlePerformanceUpdated = () => {
        console.log("Model performance updated, refreshing...")
        fetchModels()
      }

      provider.on(modelRegisteredFilter, handleModelRegistered)
      provider.on(performanceUpdatedFilter, handlePerformanceUpdated)

      return () => {
        provider.off(modelRegisteredFilter, handleModelRegistered)
        provider.off(performanceUpdatedFilter, handlePerformanceUpdated)
      }
    } catch (err) {
      console.error("Error setting up event listeners:", err)
      // Fallback to polling only
    }
  }, [fetchModels])

  return {
    models,
    loading,
    error,
    refetch: fetchModels,
  }
}

/**
 * Hook to fetch a single model's data
 */
export function useModel(modelId: number) {
  const [model, setModel] = useState<LeaderboardModel | null>(null)
  const [loading, setLoading] = useState(true)
  const [metrics, setMetrics] = useState<ModelMetrics | null>(null)

  useEffect(() => {
    async function fetchModel() {
      try {
        const provider = getProvider()
        const registry = getRegistryContract(provider)
        const marketplace = getMarketplaceContract(provider)

        const [modelInfo, modelMetrics, modelStats] = await Promise.all([
          registry.getModelInfo(modelId),
          registry.getModelMetrics(modelId),
          marketplace.getModelStats(modelId).catch(() => ({ subscribers: 0, signals: 0, monthlyPrice: 0n, signalPrice: 0n })),
        ])

        // Get latest signal
        let latestSignal: { action: "BUY" | "SELL" | "HOLD"; asset: string } | undefined
        try {
          const signals = await marketplace.getLatestSignals(modelId, 1)
          if (signals.length > 0) {
            latestSignal = {
              action: signals[0].action as "BUY" | "SELL" | "HOLD",
              asset: signals[0].asset,
            }
          }
        } catch {
          // No signals
        }

        const modelName = modelInfo.modelURI.includes("Qm")
          ? `Model #${modelId}`
          : modelInfo.modelURI.split("/").pop() || `Model #${modelId}`

        setModel({
          id: modelId,
          name: modelName,
          owner: modelInfo.owner,
          strategy: strategies[modelId % strategies.length],
          accuracy: Number(modelMetrics.accuracy) / 100,
          totalInferences: Number(modelInfo.totalInferences),
          revenue: Number(formatEther(modelMetrics.totalRevenue)),
          subscribers: Number(modelStats.subscribers),
          icon: strategyIcons[modelId % Object.keys(strategyIcons).length],
          inferencePrice: modelInfo.inferencePrice,
          latestSignal,
        })

        setMetrics({
          correctPredictions: Number(modelMetrics.correctPredictions),
          totalPredictions: Number(modelMetrics.totalPredictions),
          totalPnL: modelMetrics.totalPnL,
          totalRevenue: modelMetrics.totalRevenue,
          accuracy: Number(modelMetrics.accuracy) / 100,
        })
      } catch (err) {
        console.error("Error fetching model:", err)
        toast.error("Failed to fetch model data")
      } finally {
        setLoading(false)
      }
    }

    if (modelId) {
      fetchModel()
      // Poll for updates every 5 seconds
      const interval = setInterval(fetchModel, 5000)
      return () => clearInterval(interval)
    }
  }, [modelId])

  return { model, metrics, loading }
}

