"use client"

import { useState, useEffect, useCallback } from "react"
import { Contract, formatEther, JsonRpcProvider } from "ethers"
import { getProvider, getCompetitionContract } from "@/lib/web3"
import { CONTRACTS, COMPETITION_ABI, Competition, LeaderboardEntry } from "@/lib/contracts"
import toast from "react-hot-toast"

/**
 * Hook to fetch and manage real-time competition data
 */
export function useCompetitions(refreshInterval: number = 10000) {
  const [competitions, setCompetitions] = useState<Competition[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  const fetchCompetitions = useCallback(async () => {
    try {
      const provider = getProvider()
      const competition = getCompetitionContract(provider)

      // Get active competition IDs
      const activeIds = await competition.getActiveCompetitions()
      const competitionCount = await competition.competitionCount()

      // Fetch all competitions (active + completed)
      const allCompetitions: Competition[] = []

      for (let i = 0; i < Number(competitionCount); i++) {
        try {
          const comp = await competition.getCompetition(i)
          
          // Determine status
          const now = Math.floor(Date.now() / 1000)
          let status: "upcoming" | "active" | "ended"
          
          if (comp.status === 0) status = "upcoming" // Pending
          else if (comp.status === 1) status = "active" // Active
          else if (comp.status === 2) status = "ended" // Completed
          else status = "ended" // Cancelled

          allCompetitions.push({
            id: i,
            name: comp.name,
            startTime: Number(comp.startTime),
            endTime: Number(comp.endTime),
            prizePool: comp.prizePool,
            entryFee: comp.entryFee,
            participants: Number(comp.totalParticipants),
            status,
          })
        } catch (err) {
          console.error(`Error fetching competition ${i}:`, err)
        }
      }

      setCompetitions(allCompetitions)
      setError(null)
    } catch (err) {
      console.error("Error fetching competitions:", err)
      setError(err instanceof Error ? err.message : "Failed to fetch competitions")
      toast.error("Failed to fetch competitions")
    } finally {
      setLoading(false)
    }
  }, [])

  useEffect(() => {
    fetchCompetitions()
  }, [fetchCompetitions])

  // Set up polling
  useEffect(() => {
    const interval = setInterval(() => {
      fetchCompetitions()
    }, refreshInterval)

    return () => clearInterval(interval)
  }, [fetchCompetitions, refreshInterval])

  return {
    competitions,
    loading,
    error,
    refetch: fetchCompetitions,
  }
}

/**
 * Hook to fetch competition leaderboard
 */
export function useCompetitionLeaderboard(competitionId: number | null, refreshInterval: number = 5000) {
  const [leaderboard, setLeaderboard] = useState<LeaderboardEntry[]>([])
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    if (competitionId === null || competitionId === undefined) {
      setLeaderboard([])
      setLoading(false)
      return
    }

    async function fetchLeaderboard() {
      try {
        setLoading(true)
        const provider = getProvider()
        const competition = getCompetitionContract(provider)
        const { getRegistryContract } = await import("@/lib/web3")
        const registry = getRegistryContract(provider)

        const [modelIds, scores] = await competition.getLeaderboard(competitionId)

        if (modelIds.length === 0) {
          setLeaderboard([])
          setLoading(false)
          return
        }

        const entries: LeaderboardEntry[] = await Promise.all(
          modelIds.map(async (modelId: bigint, index: number) => {
            try {
              const score = await competition.getModelScore(competitionId, modelId)
              const owner = await registry.ownerOf(modelId)
              
              // Get model name
              const modelInfo = await registry.getModelInfo(modelId)
              const modelName = modelInfo.modelURI.includes("Qm")
                ? `Model #${Number(modelId)}`
                : modelInfo.modelURI.split("/").pop() || `Model #${Number(modelId)}`

              return {
                modelId: Number(modelId),
                modelName,
                owner,
                score: Number(scores[index] || 0),
                correctPredictions: Number(score.correctPredictions),
                totalPredictions: Number(score.totalPredictions),
                totalPnL: score.totalPnL,
              }
            } catch (err) {
              console.error(`Error fetching leaderboard entry for model ${modelId}:`, err)
              return null
            }
          })
        )

        // Sort by score descending
        const validEntries = entries
          .filter((e): e is LeaderboardEntry => e !== null)
          .sort((a, b) => b.score - a.score)

        setLeaderboard(validEntries)
      } catch (err) {
        console.error("Error fetching leaderboard:", err)
        // Don't show toast on every error to avoid spam
        setLeaderboard([])
      } finally {
        setLoading(false)
      }
    }

    fetchLeaderboard()
    const interval = setInterval(fetchLeaderboard, refreshInterval)
    return () => clearInterval(interval)
  }, [competitionId, refreshInterval])

  return { leaderboard, loading }
}

